<template>
  <div class="row" v-if="grid.length > 0">
    <div class="cell"></div>
    <div
      class="cell"
      v-for="(_, cellIndex) in grid[0]"
      v-bind:key="'col-label-' + cellIndex"
    >
      {{ String.fromCharCode(65 + cellIndex) }}
    </div>
  </div>
  <div
    class="row"
    v-for="(row, rowIndex) in grid"
    v-bind:key="'row-' + refreshCount + '-' + rowIndex"
  >
    <div class="cell">{{ rowIndex + 1 }}</div>
    <template v-for="(cell, cellIndex) in row" v-bind:key="'cell-' + cellIndex">
      <Cell class="cell" v-bind:text="cell" v-if="!!cell"></Cell>
      <div class="cell" v-else></div>
    </template>
  </div>
</template>

<script lang="ts">
import { Options, prop, Vue } from "vue-class-component";
import Cell from "./Cell.vue";

class Props {
  names = prop<string[]>({
    required: true
  });
}

@Options({
  components: {
    Cell
  }
})
export default class Grid extends Vue.with(Props) {
  refreshCount = 0;

  get grid(): string[][] {
    const area = this.names.length;
    this.refreshCount++;
    if (area <= 0) return [];
    const shuffledList = this.shuffleList(this.names);

    const colCount = Math.ceil(Math.sqrt(area));
    const rowCount = Math.ceil(area / colCount);

    return new Array(rowCount).fill([]).map((_, index) => {
      const row: string[] = shuffledList.slice(
        index * colCount,
        (index + 1) * colCount
      );
      if (row.length < colCount) {
        row.push(...new Array(colCount - row.length).fill(""));
      }
      return row;
    });
  }

  /**
   * Uses the Fisher–Yates shuffle. Go wiki it.
   */
  shuffleList(list: string[]): string[] {
    const shuffledList = [...list];
    for (let i = shuffledList.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      const temp = shuffledList[i];
      shuffledList[i] = shuffledList[j];
      shuffledList[j] = temp;
    }
    return shuffledList;
  }
}
</script>

<style scoped>
.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.cell {
  height: 100px;
  width: 100px;
  border: 1px solid gray;
  margin: 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
