<template>
  <Input @namesUpdated="updateGrid" />
  <Grid :names="names" />
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import Grid from "./components/Grid.vue";
import Input from "./components/Input.vue";

@Options({
  components: {
    Grid,
    Input
  }
})
export default class App extends Vue {
  names: string[] = [];

  updateGrid(names: string[]) {
    this.names = names;
  }
}
</script>

