<template>
  <div v-bind:class="{ box: !clicked }" v-on:click="clickMe()">
    <div v-if="clicked">{{ text }}</div>
  </div>
</template>

<script lang="ts">
import { Options, prop, Vue } from "vue-class-component";

class Props {
  text = prop<string>({ required: true });
}

@Options({})
export default class Cell extends Vue.with(Props) {
  clicked = false;

  public clickMe() {
    this.clicked = true;
  }
}
</script>

<style scoped>
.box {
  background-color: red;
}

.box:hover {
  background-color: green;
}

.box:active {
  background-color: blue;
}
</style>
