<template>
  <form class="form">
    <div
      class="text-container"
      v-for="(name, index) in names"
      :key="'input-' + index"
    >
      <input
        class="text-input"
        type="text"
        v-model="names[index]"
        placeholder="Name"
      />
      <button type="button" tabindex="-1" @click="deleteName(index)">-</button>
    </div>

    <div class="button-group">
      <button type="button" class="button-item" @click="addName()">
        +
      </button>
      <button
        type="button"
        class="button-item"
        :disabled="!isValid"
        @click="onSubmit()"
      >
        Shuffle!
      </button>
    </div>
  </form>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";

@Options({
  emits: {
    namesUpdated(names: string[]): string[] {
      return names;
    }
  }
})
export default class Input extends Vue {
  names: string[] = [""];

  addName(): void {
    this.names.push("");
  }

  deleteName(index: number): void {
    this.names.splice(index, 1);
  }

  get isValid(): boolean {
    return this.names.length > 0 && this.names.every(name => !!name);
  }

  onSubmit(): void {
    this.$emit("namesUpdated", [...this.names]);
  }
}
</script>

